import { ADD_USER, REMOVE_USER_FROM_CHANNEL, LOAD_USERSONLINE, LOAD_USERSONLINE_FAIL, LOAD_USERSONLINE_SUCCESS, RECEIVE_USER } from '../constants/ActionTypes';

const initialState = {
  loaded: false,
  data: []
};

// currently ADD USER TO CHANNEL = USER IS ONLINE
export default function onlineUsers(state = initialState, action) {
  switch (action.type) {

  case RECEIVE_USER:
    if (state.data.filter(user => user.username === action.user.username).length !== 0) {
      return state;
    }
    return {...state,
      data: [...state.data, {
        username: action.user.username,
        id: (action.user.id) ? action.user.id : state.data[state.data.length - 1].id + 1
      }]
    };

  case ADD_USER:
    console.log(action);
    return {...state,
      data: [...state.data, {
        username: action.user.username,
        id: action.user.id
      }]
    }

  case REMOVE_USER_FROM_CHANNEL:
    console.log(action)
    return {...state,
      data: state.data.filter(data => {
        return data.username !== action.user;
      })
    };

  case LOAD_USERSONLINE:
    return {...state,
      loading: true
    };

  case LOAD_USERSONLINE_SUCCESS:
    return {...state,
      loading: false,
      loaded: true,
      data: action.result
    };

  case LOAD_USERSONLINE_FAIL:
    return {...state,
      loading: false,
      loaded: false,
      error: action.error,
    };

  default:
    return state;
  }
}



